import axios from 'axios'

// axios.defaults.baseURL = 'http://api.duyiedu.com'
axios.defaults.baseURL = 'https://www.easy-mock.com/mock/5d034f2d083c0e56b73eb400/mtapp_i'
// axios.defaults.withCredentials = true;
axios.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么
  // console.log(config);
  config.params = {
    ...config.params,
    appkey: 'rolginLuo_1557471737840'
  }
  return config
}, function (error) {
  // 对请求错误做些什么
  return Promise.reject(error)
})
axios.create({
  withCredentials:true
})

export default axios



